<template>
  <el-dialog
    title="创建答疑"
    :visible="visible"
    :before-close="handleClose"
    width="500px"
  >
    <el-form ref="form" label-width="80px">
      <el-form-item label="答疑名称">
        <el-input placeholder="请输入答疑名称"> </el-input>
      </el-form-item>
    </el-form>
    <span slot="footer" class="dialog-footer">
      <el-button @click="handleClose">取 消</el-button>
      <el-button type="primary" @click="submit">确 定</el-button>
    </span>
  </el-dialog>
</template>

<script>
export default {
  name: 'Question',
  props: {
    visible: {
      type: Boolean,
      default: false
    }
  },
  methods: {
    handleClose() {
      this.$refs.form.resetFields()
      this.$emit('close')
    },
    submit() {
      this.handleClose()
    }
  }
}
</script>

<style lang="less" scoped></style>
